@charset "UTF-8";
/*HEADER PRIMARIO*/
.headerprimario {
  background-color: #2d1d1d;
}
@media (max-width: 768px) {
  .headerprimario {
    display: none;
  }
}
.headerprimario .row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.headerprimario .row .col-4 .nav {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-end;
  align-items: center;
  gap: 10px;
}
.headerprimario .row .col-4 .nav .nav-item .nav-link {
  color: #fff;
  background-color: #000;
  margin: 5px;
  font-size: 11px;
  border: 1.5px solid #fff;
}

/*ESTILOS PARA EL HEADER*/
.header img {
  max-width: 60px;
}

/*ESTILOS PERSONALES PARA EL MENÚ PRINCIPAL*/
.menu-ppal {
  background-color: #12100f;
}
.menu-ppal .container-fluid {
  flex-direction: column;
}
@media (max-width: 768px) {
  .menu-ppal .container-fluid {
    flex-direction: row;
  }
}
.menu-ppal .container-fluid .navbar-brand {
  margin: unset;
}
.menu-ppal .container-fluid .navbar-brand img {
  max-width: 130px;
  width: 100%;
}
@media (max-width: 768px) {
  .menu-ppal .container-fluid .navbar-brand img {
    max-width: 70px;
  }
}
.menu-ppal .container-fluid .navbar-toggler {
  color: #fff;
}
.menu-ppal .container-fluid #navbarNavDropdown {
  align-content: center;
  justify-content: center;
  align-items: center;
}
.menu-ppal .container-fluid #navbarNavDropdown .navbar-nav {
  gap: 1rem;
  background-color: #12100f;
}
.menu-ppal .container-fluid #navbarNavDropdown .navbar-nav .nav-item {
  text-transform: uppercase;
  font-weight: 600;
  height: 125px;
  min-width: 126px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  align-items: center;
}
.menu-ppal .container-fluid #navbarNavDropdown .navbar-nav .nav-item:hover, .menu-ppal .container-fluid #navbarNavDropdown .navbar-nav .nav-item.active {
  background-color: #fff;
}
.menu-ppal .container-fluid #navbarNavDropdown .navbar-nav .nav-item:hover .nav-link, .menu-ppal .container-fluid #navbarNavDropdown .navbar-nav .nav-item.active .nav-link {
  color: red;
}
.menu-ppal .container-fluid #navbarNavDropdown .navbar-nav .nav-item .nav-link {
  color: #fff;
  text-decoration: underline;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  gap: 10px;
  align-items: center;
}
.menu-ppal .container-fluid #navbarNavDropdown .navbar-nav .nav-item .nav-link:hover, .menu-ppal .container-fluid #navbarNavDropdown .navbar-nav .nav-item .nav-link.active {
  background-color: #fff;
}
.menu-ppal .container-fluid #navbarNavDropdown .navbar-nav .nav-item .nav-link:hover .nav-link, .menu-ppal .container-fluid #navbarNavDropdown .navbar-nav .nav-item .nav-link.active .nav-link {
  color: red;
}
.menu-ppal .container-fluid #navbarNavDropdown .navbar-nav .nav-item .dropdown-menu.show {
  top: 100%;
  background-color: #12100f;
}
.menu-ppal .container-fluid #navbarNavDropdown .navbar-nav .nav-item .dropdown-menu .nav-item {
  height: fit-content;
  color: #fff;
}
.menu-ppal .container-fluid #navbarNavDropdown.collapse.show .navbar-nav {
  gap: 0;
}
.menu-ppal .container-fluid #navbarNavDropdown.collapse.show .navbar-nav .nav-item {
  height: fit-content;
}
.menu-ppal .container-fluid #navbarNavDropdown.collapse.show .navbar-nav .nav-item .nav-link {
  justify-content: flex-start;
  padding: 1rem;
}
.menu-ppal .container-fluid #navbarNavDropdown.collapse.show .navbar-nav .nav-item.dropdown {
  justify-content: flex-start;
}
.menu-ppal .container-fluid #navbarNavDropdown.collapse.show .navbar-nav .nav-item.dropdown .dropdown-menu {
  padding: 1rem;
  width: 100%;
}